*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
}

@font-face {
  font-display: swap;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/open-sans-300.woff2") format("woff2"), url("../fonts/open-sans-300.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/open-sans-regular.woff2") format("woff2"), url("../fonts/open-sans-regular.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/open-sans-700.woff2") format("woff2"), url("../fonts/open-sans-700.woff") format("woff");
}
.container {
  outline-color: rgb(108, 18, 224);
  outline-width: 3px;
  outline-style: dotted;
  outline-offset: 0;
  width: 320px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 20px;
  padding-left: 20px;
}
@media (min-width: 660px) {
  .container {
    width: 660px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1200px;
    padding-right: 130px;
    padding-left: 130px;
  }
}

.page {
  min-width: 320px;
  height: 100%;
  font-family: "Open Sans", "Verdana", sans-serif;
  font-size: 14px;
}

.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100%;
  margin: 0;
}

.page__title {
  margin: 0;
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
}
@media (min-width: 660px) {
  .page__title {
    font-size: 40px;
    line-height: 54px;
  }
}
@media (min-width: 1200px) {
  .page__title {
    font-size: 50px;
    line-height: 68px;
  }
}

.header {
  position: absolute;
  display: grid;
  grid-template-columns: 1fr 280px 1fr;
  column-gap: 20px;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.3);
  outline-color: pink;
  outline-width: 2px;
  outline-style: solid;
  outline-offset: -2px;
}
@media (min-width: 660px) {
  .header {
    grid-template-columns: 1fr 620px 1fr;
    column-gap: 20px;
  }
}
@media (min-width: 1200px) {
  .header {
    grid-template-columns: 1fr 940px 1fr;
    column-gap: 130px;
  }
}
.header--active {
  background-color: #283645;
}

.header__top {
  display: grid;
  grid-template-columns: 1fr min-content;
  grid-template-rows: 45px 1fr;
  grid-column: 2/3;
  width: 100%;
  align-items: end;
  padding-bottom: 21px;
}
@media (min-width: 660px) {
  .header__top {
    grid-template-rows: 67px 1fr;
    padding-bottom: 43px;
  }
}

.header__toggler {
  border: none;
  padding: 0;
  width: 50px;
  height: 24px;
  background-color: #ffffff;
  mask: url("../../img/ico/btn-menu-close.svg") no-repeat right;
}
@media (min-width: 1200px) {
  .header__toggler {
    display: none;
  }
}
.header__toggler:hover {
  cursor: pointer;
  background-color: #d22856;
}
.header__toggler:active {
  background-color: rgba(210, 40, 86, 0.3);
}

.body {
  font-size: 14px;
  line-height: 24px;
}

.main {
  outline-color: pink;
  outline-width: 2px;
  outline-style: solid;
  outline-offset: -2px;
}

.footer {
  outline-color: pink;
  outline-width: 2px;
  outline-style: solid;
  outline-offset: -2px;
}

.logo {
  display: block;
}

.logo__image {
  display: block;
}
@media (min-width: 660px) {
  .logo__image {
    width: 145px;
  }
}
@media (min-width: 1200px) {
  .logo__image {
    width: 147px;
  }
}

.hero {
  color: #ffffff;
  background-color: #1d2631;
}

.hero__button {
  width: 100%;
  margin-bottom: 20px;
}

.hero__list {
  margin: 0;
  display: flex;
  align-items: center;
  padding: 0;
  list-style-type: none;
}

.hero__item {
  display: block;
}

.hero__link {
  display: block;
  outline: 1px dotted violet;
  background-position: center;
  background-repeat: no-repeat;
}
.hero__link--apple {
  width: 83px;
  height: 80px;
  background-image: url("../../img/logos/apple.svg");
}
.hero__link--google {
  width: 79px;
  height: 80px;
  background-image: url("../../img/logos/android.svg");
}
.hero__link--ms {
  width: 80px;
  height: 80px;
  background-image: url("../../img/logos/microsoft.svg");
}

.hero__note {
  font-weight: 300;
  margin: 16px 0 0;
  text-align: center;
}
@media (min-width: 660px) {
  .hero__note {
    font-size: 18px;
    line-height: 24px;
  }
}

.hero-index {
  display: grid;
  grid-template-columns: 1fr 280px 1fr;
  grid-template-rows: 462px 1fr;
  column-gap: 20px;
  background-image: url("../../img/index/promo-hand-mobile@1x.png"), url("../../img/index/promo-back-mobile@1x.jpg");
  background-repeat: no-repeat, no-repeat;
  background-position: 50% 176px, center 0;
  background-size: auto, auto 462px;
}
@media (min-width: 660px) {
  .hero-index {
    grid-template-columns: 1fr repeat(2, 300px) 1fr;
    grid-template-rows: min-content;
    min-height: 729px;
    background-image: url("../../img/index/promo-hand-tablet@1x.png"), url("../../img/index/promo-back-tablet@1x.jpg");
    background-position: 50% bottom, center 0;
    background-size: auto, cover;
  }
}
@media (min-width: 1200px) {
  .hero-index {
    grid-template-columns: 1fr repeat(3, 300px) 1fr;
    min-height: 853px;
    background-image: url("../../img/index/promo-hand-desktop@1x.png"), url("../../img/index/promo-back-desktop@1x.jpg");
    background-position: calc(50% + 70px) bottom, center 0;
  }
}

.hero-index__content {
  grid-column: 2/3;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 28px;
  padding-bottom: 31px;
}

.hero-index__title {
  grid-column: 2/3;
  padding-top: 95px;
}
@media (min-width: 660px) {
  .hero-index__title {
    padding-top: 200px;
  }
}

.nav {
  grid-column: 1/-1;
  outline-color: lime;
  outline-width: 1px;
  outline-style: solid;
  outline-offset: 0;
  width: 100%;
}

.nav__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 1200px) {
  .nav__list {
    flex-direction: row;
  }
}

.nav__item {
  display: flex;
}

.nav__link {
  outline-color: yellow;
  outline-width: 1px;
  outline-style: solid;
  outline-offset: 0;
  width: 100%;
  font-size: 18px;
  font-weight: 700;
  line-height: 32px;
  padding: 16px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
}

.advantages__list-wrapper {
  background-color: #f2f2f2;
}

.advantages__list {
  list-style-type: none;
}

.advantages__item--add {
  display: none;
}
@media (min-width: 660px) {
  .advantages__item--add {
    display: inherit;
  }
}

.advantages__name {
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
}
@media (min-width: 1200px) {
  .advantages__name {
    font-size: 30px;
    line-height: 1;
  }
}
.advantages__name--add {
  font-size: 30px;
  line-height: 1;
}

@media (min-width: 1200px) {
  .advantages__additional {
    background-color: #f2f2f2;
  }
}

.rates {
  background-color: #f2f2f2;
}

.map {
  height: 200px;
  overflow: hidden;
}
@media (min-width: 660px) {
  .map {
    height: 374px;
  }
}
@media (min-width: 1200px) {
  .map {
    height: 502px;
  }
}

.map__image {
  width: 100%;
  background-size: cover;
}

.page-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 18px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1;
  text-decoration: none;
  background-color: transparent;
  border-radius: 30px;
  outline-width: 3px;
  outline-style: solid;
  outline-offset: -3px;
}
.page-button--hero {
  color: #ffffff;
  outline-color: #ffffff;
}